.custom-anchor-scroll-section{
  margin-bottom: 98px;
  @include media(">=widescreen") {
    margin-bottom: 77px;
  }
  .title-global-section{
    margin-bottom: 40px;
    @include media(">=widescreen") {
      margin-bottom: 74px;
    }
  }
  .scroll-holder-block{
    display: flex;
    outline: none;
    padding-bottom: 84px !important;
    @include media(">=tablet") {
      margin: 0;
    }
    @include media(">=widescreen") {
      padding-bottom: 122px !important;
    }
    &:after{
      content: '';
      position: absolute;
      height: 1px;
      bottom: 90px;
      left: -9999px;
      right: -9999px;
      background: #BFBFC9;
      z-index: -1;
      @include media(">=widescreen") {
        bottom: 128px;
      }
    }
    &.scroll-pane{
      display: block;
      .jspPane{
        display: flex;
        align-items: center;
      }
    }
    .preview-block-holder{
      flex-shrink: 0;
      padding: 0 7px;
      width: 100%;
      max-width: 375px;
      margin-left: -3px;
      @include media(">=tablet") {
        width: auto;
        max-width: none;
        padding: 0 10px;
      }
      @include media(">=widescreen") {
        padding: 35px;
      }
      &:first-child{
        margin-left: 0;
        @include media(">=widescreen") {
          padding-left: 55px;
        }
      }
      &:last-child{
        @include media(">=widescreen") {
          padding-right: 55px;
        }
      }
      &:not(.bg-black){
        .preview-block-popup{
          background: transparent;
          border: 4px solid #EEEBE5;
          .btn{
            background: rgba(230, 227, 218, 0.5);
          }
        }
      }
      &.bg-black{
        .preview-block-popup{
          .btn{
            background: rgba(230, 230, 230, 0.8);
            color: #3F403E;
          }
        }
      }
      &.active{
        position: relative;
        .preview-block-popup{
          @include media(">=widescreen") {
            transform: scale(1.2);
          }
        }
      }
      &.preview-block-small{
        .preview-block-popup{
          @include media(">=widescreen") {
            padding: 89px 42px 97px;
          }
        }
      }
    }
    .preview-block-popup{
      padding: 66px 40px 51px;
      height: 275px;
      max-width: 375px;
      @include media(">=tablet") {
        height: 316px;
        max-width: 575px;
      }
      @include media(">=widescreen") {
        padding: 68px 120px 67px;
      }
      .preview-image{
        height: 100%;
        width: 100%;
        text-align: center;
        img{
          @include media(">=tablet") {
            width: auto;
            max-width: none;
            height: 100%;
          }
        }
      }
    }
  }
}
.custom-container-scroll-holder{
  margin: 0 -20px;
  position: relative;
}

.anchor-nav{
  list-style-type: none;
  padding: 0;
  margin: 0 0 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  li{
    margin: 0 13px;
  }
  a{
    font-size: 18px;
    color: $dark-gray;
    text-decoration: none;
    position: relative;
    font-weight: 500;
    line-height: 1.4;
    @include media(">=desktop") {
        font-size: 20px;
    }
    &.active{
      color: $black;
      &:after{
        opacity: 1;
        visibility: visible;
      }
    }
    &:after{
      content: '';
      width: 6px;
      height: 6px;
      border-radius: 50%;
      background: $green;
      position: absolute;
      left: 50%;
      bottom: -10px;
      transform: translateX(-50%);
      opacity: 0;
      visibility: hidden;
      transition: 0.3s;
    }
  }
}