.contact-section{
  margin-bottom: 100px;
  @include media(">=widescreen") {
    margin-bottom: 165px;
  }
  .text-holder{
    margin-bottom: 52px;
    p{
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  .title-block{
    font-size: 30px;
    font-weight: 700;
    line-height: 1.2;
    margin-bottom: 8px;
    @include media(">=widescreen") {
        font-size: 42px;
    }
  }
  .text-block{
    font-size: 16px;
    font-weight: 400;
  }
}

.contact-holder{
  @include media(">=tablet") {
    display: flex;
    margin: 0 -10px;
  }
  @include media(">=widescreen") {
    margin: 0 -20px;
  }
  .contact-text-block{
    margin-bottom: 20px;
    width: 100%;
    @include media(">=tablet") {
      margin-bottom: 0;
      padding: 0 10px;
      max-width: 50%;
    }
    @include media(">=desktop") {
        max-width: 480px;
    }
    @include media(">=widescreen") {
      padding: 0 20px;
    }
  }
  .contact-text-holder{
    padding: 20px 20px 30px;
    height: 100%;
    @include media(">=widescreen") {
      padding: 45px 30px 30px;
    }
    &.bg-green{
      a, p{
        color: $white;
      }
    }
    .image-block{
      margin: 0 -9px 40px;
      @include media(">=widescreen") {
        margin: 0 -19px 75px;
      }
    }
    a, p{
      font-size: 16px;
      font-weight: 400;
      text-transform: uppercase;
      margin-bottom: 20px;
      line-height: 1.2;
      &:last-child{
        margin-bottom: 0;
      }
    }
    a{
      @extend .hover-decor-line;
      display: inline-block;
      vertical-align: top;
      text-decoration: none;
    }
  }
  .contact-form-block{
    width: 100%;
    @include media(">=tablet") {
      padding: 0 10px;
      max-width: 50%;
    }
    @include media(">=desktop") {
      max-width: calc(100% - 480px);
    }
    @include media(">=widescreen") {
      padding: 0 20px;
    }
  }
  .contact-form-message{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 400;
    color: $white;
    text-transform: uppercase;
    position: absolute;
    text-align: center;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 10;
    padding: 20px 30px;
    p{
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  .contact-form-holder{
    padding: 15px 25px 40px;
    border: 1px solid $gray-2;
    height: 100%;
    position: relative;
    @include media(">=widescreen") {
        border-width: 2px;
    }
    .form-row{
      margin-bottom: 29px;
    }
    p{
      font-size: 16px;
      font-weight: 400;
      margin-bottom: 45px;
    }
    label{
      cursor: pointer;
      color: #a9a9a9;
      display: block;
      margin-bottom: 11px;
    }
    .form-control{
      border-color: $gray-2;
      margin-bottom: 0;
      padding-top: 16px;
      padding-bottom: 16px;
      @include media(">=widescreen") {
        border-width: 2px;
      }
    }
    textarea{
      height: 165px;
      min-height: 165px;
    }
    [type="submit"]{
      margin-top: 10px;
    }
  }
}