.plan-section{
  margin-bottom: 160px;
  @include media(">=widescreen") {
    margin-bottom: 208px;
  }
  .title-global-section{
    margin-bottom: 20px;
    @include media(">=widescreen") {
      margin-bottom: 23px;
    }
  }
  .text-block{
    font-size: 16px;
    line-height: 1.65;
    @include media(">=widescreen") {
        font-size: 22px;
    }
    p{
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  .text-holder{
    margin-bottom: 60px;
    @include media(">=widescreen") {
      margin-bottom: 80px;
    }
  }
  .table-holder{
    @include media(">=tablet") {
      margin: 0 -10px;
    }
    @include media(">=widescreen") {
      margin: 0 -20px;
    }
  }
}

.table-phone{
  @include media(">=tablet") {
    display: none !important;
  }
}
.table-desktop{
  @include media(">=tablet") {
      display: table !important;
  }
}

.plan-table-header{
  border: 1px solid $gray;
  text-align: left;
  margin: 0 -20px;
  padding-bottom: 24px;
  @include media(">=tablet") {
    margin: 0;
    padding-bottom: 0;
  }
  .table-header-head{
    position: relative;
    border-bottom: 1px solid $gray;
    padding: 11px 60px 9px 40px;
    @include media(">=tablet") {
      padding: 11px 60px 9px 10px;
    }
    @include media(">=widescreen") {
      padding: 11px 60px 9px 40px;
    }
  }
  .table-title-block{
    font-size: 20px;
    line-height: 1.35;
    font-weight: 600;
    margin: 0;
    padding-left: 15px;
    @include media(">=tablet") {
      font-size: 16px;
      padding-left: 0;
    }
    @include media(">=widescreen") {
        font-size: 24px;
    }
  }
  .open-close-opener{
    position: absolute;
    width: 50px;
    border: none;
    background: transparent;
    border-left: 1px solid $gray;
    top: 0;
    bottom: 0;
    right: 0;
    outline: none;
    &.is-open{
      &:after{
        background-image: url("../images/icon-closed.svg");
      }
    }
    &:after{
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 16px;
      height: 16px;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      background-size: contain;
      background-image: url("../images/icon-tooltip.svg");
    }
  }
  .table-header-body{
    position: relative;
  }
  .open-close-content{
    background: $white;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 37px 20px 20px 40px;
    font-size: 16px;
    color: $black-3;
    font-weight: 400;
    line-height: 1.6;
    opacity: 0;
    visibility: hidden;
    z-index: -1;
    @include media(">=tablet") {
      padding: 20px 20px;
      font-size: 14px;
    }
    @include media(">=widescreen") {
      padding: 37px 20px 20px 40px;
      font-size: 16px;
    }
    &.is-open{
      opacity: 1;
      visibility: visible;
      z-index: 10;
    }
  }
  .table-price-block{
    padding: 31px 40px 40px 55px;
    font-size: 49px;
    line-height: 1;
    font-weight: 500;
    @include media(">=tablet") {
      padding: 25px 10px 30px;
      font-size: 40px;
    }
    @include media(">=widescreen") {
      padding: 37px 40px 61px;
      font-size: 56px;
    }
    span{
      font-weight: 700;
      font-size: 16px;
      @include media(">=tablet") {
        font-size: 16px;
      }
    }
  }
  .btn{
    width: calc(100% - 40px);
    margin: 0 20px;
    @include media(">=tablet") {
      width: 100%;
      margin: 0;
    }
    &.btn-green{
      color: $white;
    }
  }
}

.plan-table.table-phone{
  .plan-table-header{
    margin-bottom: -1px;
  }
  .plan-table-col{
    margin-bottom: 80px;
    &.list-bg-dark{
      .plan-table-list{
        position: relative;
        &:after{
          content: '';
          position: absolute;
          top: 0;
          bottom: 0;
          left: -20px;
          right: -20px;
          background: $light-gray;
          z-index: -1;
        }
      }
    }
  }
  .plan-table-list{
    list-style-type: none;
    padding: 0;
    margin: 0 0 37px;
  }
  .plan-table-item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    font-size: 14px;
    font-weight: 600;
    padding: 17px 0;
    line-height: 1.18;
    border-top: 1px solid $gray;
    &:last-child{
      border-bottom: 1px solid $gray;
    }
  }
  .plan-table-title{
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 18px;
  }
  .tooltip{
    margin-left: 7px;
  }
}

.plan-table.table-desktop{
  border-collapse: inherit;
  width: 100%;
  @include media(">=tablet") {
    border-spacing: 10px 0;
  }
  @include media(">=widescreen") {
    border-spacing: 20px 0;
  }
  thead th, tbody td{
      &:nth-child(1){
        width: 60px;
        @include media(">=widescreen") {
          width: 78px;
        }
      }
      &:nth-child(3), &:nth-child(4){
        width: 32%;
        @include media(">=widescreen") {
          width: 34%;
        }
      }
  }
  thead{
    th{
      padding-bottom: 60px;
    }
  }
  tbody{
    th{
      padding: 30px 0 10px;
      font-size: 18px;
      font-weight: 700;
      text-align: left;
    }
    td{
      text-align: center;
      font-size: 14px;
      font-weight: 600;
      padding: 13px 0;
      line-height: 1.18;
      border: 1px solid $gray;
      border-left-color: transparent;
      border-right-color: transparent;
      &:nth-child(odd){
        background: $light-gray;
      }
      &:nth-child(2){
        text-align: left;
      }
      .tooltip{
        margin-left: 7px;
      }
    }
  }
}

.plus{
  font-size: 20px;
  font-weight: 700;
}
.check{
  width: 20px;
  height: 16px;
  position: relative;
  display: inline-block;
  vertical-align: middle;
  &:after{
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(45deg);
    width: 8px;
    height: 16px;
    border-bottom: 2px solid $black;
    border-right: 2px solid $black;
    @include media(">=tablet") {
      width: 10px;
      height: 20px;
      border-bottom: 3px solid $black;
      border-right: 3px solid $black;
    }
  }
}
.tooltip{
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
  width: 14px;
  height: 14px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain;
  background-image: url("../images/icon-tooltip.svg");
}

.hover-tooltip{
  @include media("<tablet") {
    left: 20px !important;
    right: 20px !important;
    width: calc(100% - 40px) !important;
  }
}

.tooltip-text{
  background: $white;
  border: 1px solid $light-gray;
  padding: 10px 15px;
  border-radius: 2px;
  font-size: 14px;
  line-height: 1.2;
  box-shadow: 0 0 10px 0 rgba($black, 0.3);
  @include media(">=tablet") {
    max-width: 200px;

  }
}