.logo-banner-section{
  padding: 12px 0 12px;
  position: relative;
  margin-bottom: 80px;
  @include media(">=tablet") {
    padding: 60px 0 55px;
    margin-bottom: 140px;
  }
  @include media(">=widescreen") {
    margin-bottom: 203px;
  }
  &:after{
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 120vw;
    background: $bg-gray;
    opacity: 0.4;
    z-index: -1;
  }
  &.custom-section{
    margin-bottom: 50px;
    @include media(">=tablet") {
      padding: 27px 0 22px;
      margin-bottom: 150px;
    }
    .logo-list{
      padding: 0 20px;
      .logo-item{
        margin-right: 30px;
        @include media(">=widescreen") {
            max-width: 205px;
        }
        &:last-child{
          margin-right: 0;
        }
      }
    }
  }
}

.logo-list{
  list-style-type: none;
  padding: 20px 20px 20px 0;
  margin: 0 -20px 0 0;
  display: flex;
  align-items: center;
  width: calc(100% + 20px);
  overflow-x: auto;
  @include media(">=widescreen") {
    justify-content: space-between;
  }
  .logo-item{
    margin-right: 60px;
    min-width: 80px;
    @include media(">=tablet") {
        min-width: 135px;
    }
    &:last-child{
      margin-right: 0;
      padding-right: 20px;
      @include media("<tablet") {
        min-width: 100px;
      }
      @include media("<desktop") {
        min-width: 140px;
      }
      @include media(">=desktop") {
        padding-right: 0;
      }
    }
    &.logo-item-big{
      min-width: 100px;
      @include media(">=tablet") {
        min-width: 140px;
      }
    }
  }
  a{
    display: block;
    line-height: 0;
    opacity: 1;
    transition: 0.3s;
    &:hover{
      opacity: 0.5;
    }
  }
}