.service-banner-section{
  padding-top: 24px;
  margin-bottom: 97px;
  @include media(">=desktop") {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-end;
  }
  @include media(">=widescreen") {
    padding-top: 54px;
    margin-bottom: 162px;
    align-items: stretch;
  }
  .text-holder{
    margin-bottom: 27px;
    @include media(">=desktop") {
      margin-bottom: 72px;
    }
  }
  .title-global-section{
    margin-bottom: 15px;
    max-width: 950px;
    @include media(">=widescreen") {
      margin-bottom: 24px;
    }
    &.big-title{
      @include media(">=widescreen") {
        line-height: 1.16;
      }
    }
    strong{
      font-weight: 700;
    }
  }
  .text-block{
    font-size: 16px;
    line-height: 1.6;
    max-width: 600px;
    @include media(">=widescreen") {
        font-size: 22px;
    }
    p:last-child{
      margin-bottom: 0;
    }
  }
  .service-list{
    @include media(">=desktop") {
      max-width: 50%;
    }
    @include media(">=widescreen") {
      max-width: 550px;
    }
  }
  .image-holder{
    margin: 0 -10px;
    text-align: center;
    position: relative;
    @include media(">=desktop") {
      width: 100%;
      margin: 0;
      max-width: 50%;
      text-align: left;
    }
    @include media(">=widescreen") {
        min-height: 429px;
    }
    .image-block{
      @include media(">=desktop") {
        margin: 0 -20px;
      }
      @include media(">=widescreen") {
        position: absolute;
        margin: 0;
        right: calc(570px - 50vw);
        left: -20px;
        bottom: -50px;
      }
      picture{
        display: inline-block;
      }
      img{
        width: 100%;
        max-width: 871px;
      }
    }
  }
}

.service-list{
  display: flex;
  overflow-x: auto;
  margin: 0 -20px 28px;
  @include media(">=desktop") {
    margin: 0;
    flex-wrap: wrap;
    overflow-x: visible;
  }
  .service-item{
    flex-shrink: 0;
    width: 100%;
    max-width: 168px;
    border: 1px solid $light-gray;
    padding: 30px 10px 28px 20px;
    @include media(">=desktop") {
        max-width: 33.33%;
    }
    @include media(">=widescreen") {
      padding: 32px 10px 28px 24px;
    }
    &:first-child, &:last-child{
      background: rgba(#E6E3DA, 0.34);
    }
    &:nth-child(-n+3){
      @include media(">=desktop") {
        border-top: none;
      }
    }
    &:nth-last-child(-n+3){
      @include media(">=desktop") {
        border-bottom: none;
      }
    }
    &:nth-child(3n){
      @include media(">=desktop") {
        border-right: none;
      }
    }
    &:nth-last-child(3n){
      @include media(">=desktop") {
        border-left: none;
      }
    }
  }
  .number-block{
    font-size: 40px;
    font-weight: 300;
    line-height: 1.147;
    margin-bottom: 57px;
    padding: 0 5px;
    @include media(">=widescreen") {
      padding: 0 1px;
    }
  }
  .title-block{
    font-size: 14px;
    font-weight: 600;
    line-height: 1.1;
    margin-bottom: 11px;
    text-transform: uppercase;
    @include media(">=widescreen") {
      margin-bottom: 16px;
    }
  }
  .subtitle-block{
    font-size: 12px;
    line-height: 1.2;
    font-weight: 400;
  }
}