.slider-image-section{
  margin-bottom: 110px;
  @include media(">=widescreen") {
    margin-bottom: 176px;
    margin-top: -30px;
  }

  .slider-image{
    position: relative;
    margin: 0 -20px;
    @include media(">=tablet") {
      margin: 0;
      padding: 0;
    }
    .slide{
      padding-bottom: 505px;
      @include media(">=tablet") {
        display: flex;
        padding-bottom: 165px;
        padding-top: 94px;
      }
    }
    .slick-arrow{
      position: absolute;
      bottom: 420px;
      left: 20px;
      z-index: 100;
      @include media(">=tablet") {
        bottom: 0;
        left: 0;
      }
      &.slick-next{
        left: 120px;
        @include media(">=tablet") {
          left: 100px;
        }
      }
    }
  }
  .text-holder{
    padding-right: 20px;
    padding-left: 20px;
    @include media(">=tablet") {
      padding-left: 0;
      max-width: 50%;
    }
    @include media(">=widescreen") {
      padding-right: 110px;
    }
  }
  .title-global-section{
    margin-bottom: 27px;
    @include media(">=widescreen") {
      margin-bottom: 49px;
    }
  }
  .subtitle-block{
    font-size: 20px;
    font-weight: 600;
    line-height: 1.4;
    margin-bottom: 24px;
    color: $black-2;
    @include media(">=widescreen") {
      margin-bottom: 28px;
      font-size: 34px;
    }
  }
  .text-block{
    font-size: 16px;
    font-weight: 500;
    line-height: 1.7;
    margin-bottom: 40px;
    color: $black-3;
    p:last-child{
      margin-bottom: 0;
    }
  }
  .name-block{
    font-size: 20px;
    font-weight: 500;
  }
  .position-block{
    font-weight: 400;
    font-size: 16px;
    color: $black-3;
  }
  .image-holder{
    position: absolute;
    bottom: 0;
    left: 0;
    right: -20px;
    padding-right: 20px;
    @include media(">=tablet") {
      top: 0;
      bottom: 0;
      left: 50%;
      right: 0;
      padding-right: 0;
    }
  }
  .image-list{
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0 12px 0 20px;
    width: 100%;
    overflow-y: auto;
    @include media(">=tablet") {
      height: 100%;
      padding: 0;
      position: relative;
      overflow: visible;
    }
  }
  .image-item{
    margin-right: 12px;
    height: 360px;
    @include media(">=tablet") {
      position: absolute;
      transition: transform 0.4s linear, opacity 0.2s linear;
      height: auto;
      width: calc(100% - 50px);
      max-width: 480px;
    }
    @include media(">=widescreen") {
      width: calc(100% - 100px);
    }
    &:nth-child(odd){
      @include media(">=tablet") {
        top: 30px;
        left: 0;
        z-index: 5;
      }
    }
    &:nth-child(even){
      @include media(">=tablet") {
        bottom: 30px;
        right: 0;
        z-index: 0;
      }
    }
    &.is-animate{
      @include media(">=tablet") {
        transform: scale(1.1);
      }
      &.is-active{
        @include media(">=tablet") {
          transform: scale(0.9);
          opacity: 0;
        }
      }
    }
    &.is-active{
      @include media(">=tablet") {
        z-index: 10;
      }
    }
    img{
      max-height: 100%;
      width: auto;
      max-width: none;
      @include media(">=tablet") {
        max-height: none;
        width: 100%;
      }
    }
  }
}