.member-holder{
  border: 1px solid $gray;
  height: 100%;
}
.member-header{
  padding: 20px 18px 30px;
  display: flex;
  align-items: flex-end;
  position: relative;
  @include media(">=widescreen") {
      align-items: flex-start;
    padding: 20px 20px 30px;
  }
  .member-image{
    width: 100%;
    max-width: 89px;
    line-height: 0;
    @include media(">=widescreen") {
        max-width: 120px;
    }
    img{
      width: 100%;
    }
  }
  .member-text{
    padding: 47px 0 0 20px;
    @include media(">=widescreen") {
      padding: 47px 0 0 33px;
    }
  }
  .name-block{
    font-size: 22px;
    font-weight: 500;
    line-height: 1.45;
    margin-bottom: 7px;
  }
  .position-block{
    font-size: 16px;
    font-weight: 400;
    color: $dark-gray;
    margin-bottom: 0;
  }
  .level-block{
    position: absolute;
    top: 0;
    right: 0;
    background: $green;
    color: $white;
    font-size: 14px;
    font-weight: 600;
    line-height: 1;
    padding: 17px 19px;
  }
}

.member-info{
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-weight: 500;
  line-height: 1.7;
  .info-item{
    border-top: 1px solid $gray;
    display: flex;
    &:last-child{
      border-bottom: 1px solid $gray;
    }
  }
  .info-title{
    width: 108px;
    flex-shrink: 0;
    border-right: 1px solid $gray;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px 10px;
    min-height: 72px;
    font-size: 14px;
    @include media(">=widescreen") {
      align-items: flex-start;
      font-size: 16px;
      padding: 10px 10px 10px 25px;
      width: 139px;
    }
  }
  .info-text{
    font-size: 16px;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    padding: 10px 30px 10px 20px;
    width: calc(100% - 108px);
    @include media(">=widescreen") {
        width: calc(100% - 139px);
    }
  }
  .star-block{
    font-weight: 700;
    &:before{
      content: '';
      display: inline-block;
      vertical-align: top;
      margin-right: 4px;
      margin-top: 2px;
      width: 14px;
      height: 14px;
      background-size: contain;
      background-position: 50% 50%;
      background-repeat: no-repeat;
      background-image: url("../images/icon-star.svg");
      @include media(">=widescreen") {
        margin-top: 6px;
      }
    }
  }
}

.member-footer{
  padding: 24px 15px 31px;
  @include media(">=widescreen") {
    padding: 24px 20px 20px;
  }
  .skill-list{
    list-style-type: none;
    padding: 0;
    margin: 0 0 16px;
    display: flex;
    flex-wrap: wrap;
    li{
      margin: 0 10px 10px 0;
      border: 1px solid $gray;
      padding: 15px 18px;
      font-size: 14px;
      line-height: 1.1;
      font-weight: 500;
      @include media(">=widescreen") {
          font-size: 16px;
      }
    }
  }
  .btn{
    padding-left: 15px;
    padding-right: 15px;
    width: 100%;
    text-align: center;
    &:before{
      position: static;
      transform: translate(0, 0);
      margin-right: 10px;
      margin-top: 6px;
    }
    &:after{
      display: none;
    }
  }
}