#wrapper {
	max-width: 100%;
	overflow: hidden;
	position: relative;
}


.container{
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1220px;
  &.container-big{
    max-width: 1440px;
  }
}

.full-section{
  margin: 0 -20px;
  @include media(">=widescreen") {
    margin: 0;
    width: 100vw;
    position: relative;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

.title-global-section{
  font-size: 40px;
  font-weight: 600;
  line-height: 1.193;
  @include media(">=widescreen") {
    font-size: 56px;
  }
  &.big-title{
    font-size: 44px;
    font-weight: 400;
    line-height: 1.3;
    @include media(">=widescreen") {
        font-size: 64px;
    }
  }
  strong{
    font-weight: 900;
  }
}

.slick-arrow{
  border: none;
  background: transparent;
  padding: 0;
  font-size: 16px;
  font-weight: 600;
  color: $black;
  line-height: 1.1875;
  outline: none;
  &.slick-disabled{
    opacity: 0.4;
  }
  &.slick-prev{
    &:before{
      content: '';
      width: 8px;
      height: 8px;
      background: currentColor;
      border-radius: 50%;
      display: inline-block;
      vertical-align: top;
      margin-right: 10px;
      margin-top: 5px;
    }
  }
  &.slick-next{
    &:after{
      content: '';
      width: 8px;
      height: 8px;
      background: currentColor;
      border-radius: 50%;
      display: inline-block;
      vertical-align: top;
      margin-left: 10px;
      margin-top: 5px;
    }
  }
}

.fancybox-is-open .fancybox-bg{
  opacity: 1;
}
.fancybox-bg{
  background: #EEEBE5;
}
.popup-content{
  padding: 70px 20px;
  position: relative;
  @include media(">=tablet") {
    padding: 115px 40px;
  }
  @include media(">=widescreen") {
    width: 100%;
    max-width: 1180px;
  }
  .fancybox-close-small{
    position: fixed;
    top: 5px;
    right: 5px;
    padding: 5px;
    @include media(">=tablet") {
      top: 20px;
      right: 20px;
    }
    @include media(">=widescreen") {
      top: 48px;
      right: 60px;
      padding: 0;
    }
  }
  .preview-image{
    width: 100%;
    max-width: 770px;
    margin: 0 auto;
    text-align: center;
    line-height: 0;
  }
  .title-block{
    font-size: 16px;
    font-weight: 600;
    line-height: 1.13;
    position: absolute;
    top: 20px;
    left: 20px;
    @include media(">=tablet") {
      font-size: 24px;
      top: 40px;
      left: 60px;
    }
  }
  .caption-block{
    font-size: 12px;
    font-weight: 400;
    position: absolute;
    bottom: 12px;
    right: 12px;
    color: $black;
    @include media(">=tablet") {
      bottom: 37px;
      right: 43px;
    }
    &:before{
      content: '';
      display: inline-block;
      vertical-align: middle;
      width: 4px;
      height: 4px;
      background: currentColor;
      border-radius: 50%;
      margin-right: 8px;
      margin-top: -2px;
    }
    strong{
      font-weight: 600;
    }
  }
}

.js-tab-hidden {
  display: block !important;
  left: -9999px !important;
  position: absolute !important;
  top: -9999px !important;
}

img.grayscale {
  filter: url("data:image/svg+xml;utf8,<svg xmlns=\'http://www.w3.org/2000/svg\'><filter id=\'grayscale\'><feColorMatrix type=\'matrix\' values=\'0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0.3333 0.3333 0.3333 0 0 0 0 0 1 0\'/></filter></svg>#grayscale");
  filter: gray;
  -webkit-filter: grayscale(100%);
}

.bg-green{
  background: $green;
}